@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body
{
  font-family: 'IBM Plex Serif', serif;
}
#header a
{
  color: #fff;
}

#projects .card
{
  display: inline-block;
}
#card {
    background-color: #E6EBEE;
    height: auto;
    width: 350px;
    margin: 10vh auto;
    margin-top:20px;
    border-radius: 25px;
    padding-bottom: 1px;
    box-shadow: 2px 2px 5px #ff0066;
}

h1 {
    color: white;
    text-align: center;
    width: 100%;
    background-color: #E6EBEE;
    border-radius: 25px 25px 0 0;
    color: #393B45;
    padding: 30px 0;
    font-weight: 800;
    margin: 0;
}


#avatar {
    display: inline;
    height: 230px;
    width: 100%;
}

#bio {
    display: block;
    margin: 30px auto;
    width: 280px;
    height: auto;
}

#bio p {
    color: #333;
    font-weight: lighter;
    font-size: 15px;
    text-align: justify;
}

#stats {
    display: flex;
    flex-direction: row;
    height: auto;
    width: 280px;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    font-weight: 500;
}

.col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;
}

.stat {
    font-size: 25px;
    margin: 0;
}

.label {
    margin: 0;
}

#buttons {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    width: 280px;
}

button {
    display: block;
    position: relative;
    padding: 10px 0;
    width: 130px;
    margin: 30px 0;
    border-radius: 25px;
    border: none;
    font-size: 20px;
    letter-spacing: 0.2px;
    font-weight: 500;
    background-color: #4069E2;
    color: #E6EBEE;
}

button:focus {
    display: none;
}

button:hover {
    transform: scale(1.03);
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

#msg{
    background-color: #ff0066;
    color: #E6EBEE;
}

#form
{
    width: 350px;
    border: 1px solid #333;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    margin: auto;
    margin-top:10px;
}

#label
{
    background-color: #4069E2;
    color: #fff;
    padding: 5px;
    width: 100%;
    cursor: pointer;
}
#calculator
{
    background-color:#E6EBEE;
    height: auto;
    width: 350px;
    margin: 10vh auto;
    margin-top:20px;
    padding-bottom: 1px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
#input
{
    width: 100%;
    text-decoration: none;
    height: 60px;
    font-size: 20px;
    color: #fff;
    text-align: right;
    padding-right: 10px;
    background-color: rgb(82, 6, 6);
    caret-color: transparent;
    unicode-bidi: embed;
}
#btns button{
    width: 20%;
    border-radius: 5px;
    display: inline-block;
    margin-left: 3%;

}

.operator
{
    background-color: red;
}

.other
{
    background-color: rgb(21, 146, 21);
}
@media(min-width:800px)
{
    .form
    {
        width: 50%;
        margin: auto;
        margin-top: 30px;
    }
}

